

/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/

// Breakpoints
@mixin bp($point) {
  
	$sm: "(min-width: 576px)";
	$md: "(min-width: 768px)";
	$lg: "(min-width: 960px)";
	$xl: "(min-width: 1200px)";
  
	@if $point == sm {
	@media #{$sm} { @content; }
	}
	@else if $point == md {
	@media #{$md} { @content; }
	}

	@else if $point == lg {
	@media #{$lg} { @content; }
	}
  
	@else if $point == xl {
	@media #{$xl} { @content; }
	}
}


body {
	overflow-x:hidden;
}
:root {
	--section-margin-bottom:80px;
	@include bp(md) {
		--section-margin-bottom:80px;
	}
}

.full-container, .alignfull {
	padding: 0;
	max-width: calc(100% + 40px); /*compenser le padding de primary*/
	margin: 0 -20px var(--section-margin-bottom); 
}

.bleed-container {
	width: 100vw;
	margin-left: 50%;
	padding: 0;
	transform: translateX(-50%);
	margin-bottom:var(--section-margin-bottom);
}

/* Largeur maxi */
.entry-content {
	max-width:1190px;
	min-height:500px;
	margin:0 auto;
	padding:0 20px;
}



/* Grid */

.flex {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
}

.show-for-md,
.show-for-lg {
	display: none;
}

@include bp(md) {


	.show-for-md {
		display: initial;
	}

	.hide-for-md {
		display: none;
	}
}

@include bp(lg) {


	.show-for-lg {
		display: initial;
	}

	.hide-for-lg {
		display: none;
	}
}

